$(function () {
  function $(id) {
    return document.getElementById(id);
  }
  function generatelink() {
	var link;
    var add = $("input").value;
    var drive = add.indexOf("google.com/file/d/");
    var dbox = add.indexOf("dropbox.com/s");
    var odrive = add.indexOf("onedrive.live.com");
    if (drive != -1) {
      var start = add.indexOf("d/");
      var end = add.indexOf("/view");
      var reString = add.slice(start + 2, end);
      link =
        "https://drive.google.com/uc?export=download&id=" + reString + "";
      $("output").value = link;
    } else if (dbox != -1) {
      link = add.replace("?dl=0", "?dl=1");
      $("output").value = link;
    } else if (odrive != -1) {
      link = add.replace("redir", "download");
      $("output").value = link;
    } else {
      $("output").value = "The URL Is Invalid";
    }
  }
  window.onload = function () {
    $("input").focus();
    $("generate").onclick = generatelink;
  };
});
$(function () {
  $("#copy").click(function () {
    if (
      $("#output").val() != "The URL Is Invalid" &&
      $("#output").val() != ""
    ) {
      $("#output").select();
      document.execCommand("copy");
      bootbox.dialog({
        title: "Good Job!",
        message: "Copied To Clipboard.",
        closeButton: true,
      });
    } else {
      bootbox.dialog({
        title: "Error!",
        message: "The URL Is Invalid.",
        closeButton: true,
      });
    }
  });
  if ($(window).width() > 768) {
    $("body img").addClass("mx-auto d-block w-75");
  }
  $(".thisToggle").click(function () {
    $(this).toggleClass("fa-angle-down fa-angle-up");
  });
  deferimg("img[data-src]")
});
